// ==========================================================================
// Functions
// ==========================================================================

// Import if Google Fonts URL is defined

@if variable-exists(font-url--google) {
  @import url($font-url--google);
}

//Calculate em values

@function em($target, $context: $base-font-size) {
	@return ($target / $context) * 1em;
}

// Capitalize all letters of all first words

@function capitalize($word) {
  @return to-upper-case(str-slice($word,1,1)) + str-slice($word, 2);
}

// Call the color palette modifiers in color values

@function palette($palette, $shade: 'base') {
 	@return map-get(map-get($color-palettes, $palette), $shade);
}

// Calling the Font Family from Icomoon

@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?esuxuj');
  src:  url('fonts/icomoon.eot?esuxuj#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?esuxuj') format('truetype'),
    url('fonts/icomoon.woff?esuxuj') format('woff'),
    url('fonts/icomoon.svg?esuxuj#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}



